import _padStart from"lodash/padStart";import*as bip39 from'bip39';var mnemonicWordToValueData=function(word){var wordlist=bip39.wordlists.english;var index=wordlist.indexOf(word);if(index<0){throw new Error('Invalid mnemonic');}var binary=parseInt(`${index+1}`,10).toString(2);return _padStart(binary,12,'0').split('').map(function(bit){return bit==='1';});};export var mnemonicToDotMapValues=function(mnemonics){var usedMnemonic=mnemonics.trim().replace(/\s+/g,' ');var validMnemonic=bip39.validateMnemonic(usedMnemonic);if(!validMnemonic){throw new Error('Invalid mnemonic');}var words=usedMnemonic.split(' ');validMnemonic=bip39.validateMnemonic(words.join(' '));if(!validMnemonic){throw new Error('Invalid mnemonic');}var keyTagMnemonics=[];words.forEach(function(word,index){var keyTagMnemonic={index:index+1,values:mnemonicWordToValueData(word)};keyTagMnemonics.push(keyTagMnemonic);});return keyTagMnemonics;};